            filter=".main-ball-box"
          > -->
            <div class="main-ball-box">
                  <div :class="['main-ball',checkedClass?checkedClass:'']" id="main-ball">
                    <section class="stage" v-if="!checkedClass">
                      <figure class="ball"><span class="home-shadow"></span></figure>
                    </section>
                  </div>
            </div>
           <!-- </draggable> -->
