<script>
import draggable from "vuedraggable";
import { setTimeout } from 'timers';
export default {
  name: "",
  data() {
    return {
      // arr1:[{
      //   title:'PAINTING'
      // },{
      //   title:'ROOF'
      // },{
      //   title:'MAIN'
      // },{
      //   title:'ROOM'
      // },{
      //   title:'ALCOVE'
      // }]
      offsetLeft: 0,
      offsetTop:0,
      checkedClass:''
    };
  },

  components: {
    draggable
  },

  methods: {
    end(e){
      console.log(e)
      console.log(e.originalEvent.x,e.originalEvent.y)
      let id = Number(e.from.dataset.id);
      if(id == 1|| id == 2){
        if(e.originalEvent.x < this.offsetLeft+60 || e.originalEvent.y < this.offsetTop){
          return false;
        }
      }else{
        if(e.originalEvent.x > this.offsetLeft+this.offsetWidth || e.originalEvent.y < this.offsetTop){
          return false;
        }
      }
      console.log(id)
      this.checkedClass = 'checkedClass'+id;
      setTimeout(()=>{
      switch(id){
        case 1:
          this.$router.push({
            path:'/page1'
          })
          break;
        case 2:
          this.$router.push({
            path:'/page2'
          })
          break;
        case 3:
          this.$router.push({
            path:'/page3'
          })
          break;
        case 4:
          this.$router.push({
            path:'/page4'
          })
          break;
      }
      },900)
    },
  },
  mounted(){
    let main = document.getElementById('main-ball')
    this.offsetLeft = main.offsetLeft;
    this.offsetTop = main.offsetTop;
    this.offsetWidth = main.offsetWidth;
    console.log(main.offsetLeft,main.offsetTop)
  }
};
</script>

